@import '../../../variables';

.Carousel {
  max-width: 100%;
  &-block {
    display: flex !important;
    justify-content: center;
    padding: 10px;
    &-title {
      font-size: 60px !important;
      margin-bottom: 30px !important;
      text-shadow: 0px 0px 10px #000; }
    > div {
      position: relative;
      display: flex;
      justify-content: center;
      width: 100%;
      max-width: 1600px;
      padding: 0 20px;
      box-sizing: border-box; }
    img {
      width: 100%;
      height: 100%; }
    &-text {
      position: absolute;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      justify-content: space-between;
      top: 0;
      left: 20px;
      padding: 40px;
      width: 100%;
      box-sizing: border-box;
      color: $white; } } }

@media (max-width: 959.5px) {
  .Carousel {
    &-block {
      padding: 0;
      &-title {
        font-size: 34px !important;
        margin-bottom: 12px !important; }
      &-text {
        justify-content: flex-end;
        padding: 50px;
        bottom: 0; } } } }

@media (max-width: 600px) {
  .Carousel {
    max-height: 840px;
    width: auto;
    .slick-list {
      overflow: hidden;
      max-height: 840px; }
    &-block {
      padding: 0;
      > div {
        padding: 0; }
      &-text {
        left: 0;
        padding: 20px; } } } }
