@import '../../../../variables';

.MobileSearchBox {
  &-main {
    position: fixed;
    top: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    padding: 4px 12px;
    box-sizing: border-box;
    background: inherit; }

  &.fixed {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 1100;
    width: 100vw;
    height: 100vh;
    padding-top: 64px;
    padding-bottom: 100px;
    background: $white;
    box-sizing: border-box;
    overflow: hidden;
    overflow-y: scroll; } }
