@import '../../../../variables';

@mixin button($bg, $color) {
  background: $bg;
  color: $color; }

.profileBillingAddress {
  &.toAdd {
    display: flex; }
  &-header {
    width: 100%;
    position: relative;
    margin-bottom: 20px;
    padding: 15px;
    box-sizing: border-box; }

  &-preview {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    * {
      font-size: 1rem !important; }

    .openForm {
      background: $white;
      margin-right: 20px; } }

  form {
    .formBtns {
        margin-top: 40px;
        display: flex;
        justify-content: flex-end;
        button:first-child {
          background: $white;
          margin-right: 5px; } } } }


@media (max-width: 599.5px) {
  .profileBillingAddress {
    &-preview {
      flex-direction: column;
      justify-content: center;
      align-items: flex-start; }

    .openForm {
      margin-left: 20px; } } }
