@import '../../../variables';

@mixin pseudo($width, $height) {
  content: '';
  position: absolute;
  width: $width;
  height: $height; }

.DesktopMenu {
  // display: flex
  // align-items: stretch
  // flex-direction: column

  .triger {
    position: relative;
    z-index: 100;
    height: 100%;
    margin: 0;
    padding: 20px 27px;
    font-weight: 600;
    color: $primary-black;
    box-sizing: border-box;
    cursor: pointer;
    transition: .4s cubic-bezier(0.075, 0.82, 0.165, 1);
    &.active, &:hover {
      color: $black; }
    &.active::before {
      @include pseudo(100%, 3px);
      bottom: -1.5px;
      right: 0;
      background: $primary-black;
      border-radius: 20px; } } }

.menu {
  position: fixed;
  top: 64px;
  z-index: 0;
  left: -2000vw;
  display: none;
  flex-direction: row;
  justify-content: center;
  width: 100%;
  box-sizing: border-box;
  color: $primary-black;
  background: $white;
  border-top: 1px solid $light-gray;
  box-shadow: 0px 2px 4px -2px rgba(0,0,0,0.2), 0px 4px 5px 0px rgba(0,0,0,0.14);
  text-transform: capitalize;
  &.open {
    left: 0;
    display: flex; }

  .categories_blocks {
    position: relative;
    margin: 50px 0;
    padding: 20px 50px;
    transition: .5s cubic-bezier(0.075, 0.82, 0.165, 1);

    &:not(:last-child)::before {
      @include pseudo(1px, 100%);
      top: 0;
      right: 0;
      background: $light-gray; }

    &-title {
      width: 100%;
      color: $primary-black;
      cursor: pointer;
      transition: .4s; }

    .categories_blocks-title:hover,
    .subcategories_blocks-title:hover, {
      color: $black; }

    .subcategories_blocks {
      display: flex;
      flex-direction: column;
      justify-content: flex-start;

      &-title {
        position: relative;
        width: 100%;
        box-sizing: border-box;
        padding-bottom: 3px;
        cursor: pointer;
        color: $secondary-black-light;
        transition: .4s;
        &::before {
          @include pseudo(0, .5px);
          right: 0;
          bottom: 1px;
          transition: .4s;
          background: $secondary-black-light; }
        &:hover::before {
          opacity: .5;
          left: 0;
          width: 100%; } } } } }
